@use "./functions";
@use "./variables" as *;

@mixin button(
  $width: false,
  $height: false,
  $background-color: false,
  $border-radius: false,
  $border: false,
  $box-shadow: false
) {
  @if $width !=false {
    width: $width;
  }

  @if $height !=false {
    height: $height;
  }

  @if $background-color !=false {
    background-color: $background-color;
  }

  @if $border-radius !=false {
    border-radius: $border-radius;
  }

  @if $border !=false {
    border: $border;
  }

  @if $box-shadow !=false {
    box-shadow: $box-shadow;
  }

  cursor: pointer;
}

@mixin set-size($width: false, $height: false) {
  @if $width !=false {
    width: $width;
  }

  @if $height !=false {
    height: $height;
  }
}

@mixin transition($transition: false, $transition-timing-function: false) {
  @if $transition !=false {
    transition: $transition;
  }

  @if $transition-timing-function !=false {
    transition-timing-function: $transition-timing-function;
  }
}

@mixin tag_a($color: false, $text-decoration: false) {
  @if $color !=false {
    color: $color;
  }

  @if $text-decoration !=false {
    text-decoration: $text-decoration;
  }
}

@mixin grid(
  $grid-tplt-row: false,
  $grid-tplt-column: false,
  $justify-content: false,
  $align-items: false,
  $grid-gap: false,
  $column-gap: false
) {
  display: grid;

  @if $grid-tplt-row !=false {
    grid-template-rows: $grid-tplt-row;
  }

  @if $grid-tplt-column !=false {
    grid-template-columns: $grid-tplt-column;
  }

  @if $justify-content !=false {
    justify-content: $justify-content;
  }

  @if $align-items !=false {
    align-items: $align-items;
  }

  @if $grid-gap !=false {
    grid-gap: $grid-gap;
  }

  @if $column-gap !=falase {
    column-gap: $column-gap;
  }
}

@mixin font(
  $font-family: false,
  $font-size: false,
  $font-weight: false,
  $font-style: false,
  $line-height: false,
  $color: false
) {
  @if ($font-family !=false) {
    font-family: $font-family;
  }

  @if $font-size {
    font-size: $font-size;
  }

  @if $font-weight {
    font-weight: $font-weight;
  }

  @if $font-style {
    font-style: $font-style;
  }

  @if $line-height {
    line-height: $line-height;
  }

  @if $color {
    color: $color;
  }
}

@mixin flex(
  $direction: false,
  $align: false,
  $justify: false,
  $wrap: false,
  $gap: false
)
 {
  display: flex;
  @if $direction {
    flex-direction: $direction;
  }
  @if $align {
    align-items: $align;
  }

  @if $justify {
    justify-content: $justify;
  }

  @if $wrap {
    flex-wrap: $wrap;
  }

  @if $gap {
    gap: $gap;
  }
}

@mixin bg-color($color) {
  background-color: $color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

@mixin position(
  $position: false,
  $top: false,
  $bottom: false,
  $right: false,
  $left: false
) {
  @if $position != false {
    position: $position;
  }

  @if $top != false {
    top: $top;
  }
  @if $bottom != false {
    bottom: $bottom;
  }
  @if $right != false {
    right: $right;
  }
  @if $left != false {
    left: $left;
  }
}

//reponsive

@mixin tablet {
  @media screen and (min-width: $mobile-medium-max) {
    @content;
  }
}

@mixin desktop {
    @media screen and (min-width: $tablet-max) {
        @content;
    }
  }
